
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.time_slider_text  {
  width:12.5%;
  height: 5px;
  line-height: 5px;
  position: absolute;
  -moz-user-select: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.time_slider_bar  {
  width:10%;
  height: 5px;
  margin-left:2.8%;
  background-color: #e4e7ed;
  border-radius: 3px;
  position: absolute;
  cursor:pointer;
}
.time_slider_button_start {
  position:absolute;
  width: 12px;
  height: 12px;
  border: 2px solid #409eff;
  background-color: #fff;
  border-radius: 50%;
  cursor:-webkit-grab;
  cursor:grab;
  z-index:100
}
.time_slider_button_stop {
  position:absolute;
  width: 12px;
  height: 12px;
  border: 2px solid #409eff;
  background-color: #fff;
  border-radius: 50%;
  cursor:-webkit-grab;
  cursor:grab;
  z-index:100
}
